.Input,
.Select,
.Textarea {
	box-sizing: border-box;
	width: 80%;
	position: relative;
	margin: auto;
	z-index: 0;
	margin-bottom: 30px;
}

.Input input,
.Select select,
.Textarea textarea {
	width: 100%;
	height: 40px;
	font: 520 0.999em arial;
	padding: 3px;
	padding-bottom: 0;
	outline: none;
	border: none;
	border-bottom: 1px solid rgb(153, 26, 77);
	background-color: initial;
}

.Input label {
	position: absolute;
	bottom: 10px;
	left: 7px;
	color: grey;
	font: 530 0.98em arial;
	transition: all 0.5s ease-out;
}

.Textarea label {
	position: absolute;
	top: 10px;
	color: grey;
	font: 530 0.98em arial;
	transition: all 0.5s ease-out;
}

.Input input:focus + label,
.Textarea textarea:focus + label,
.hangedLabel {
	color: rgb(100, 5, 43) !important;
	transform: translateY(-25px);
	font-size: 0.88em !important;
}

.invalid input {
	box-sizing: border-box;
	background-color: rgba(139, 71, 71, 0.849) !important;
	border-bottom: 1px solid red !important;
}

.invalid label {
	color: purple;
}

.Textarea textarea {
	height: 100px;
	resize: none;
}

@media (min-width: 1000px) {
	.Input,
	.Select {
		width: 40%;
		display: inline-block;
		margin: 20px;
	}
}
